ul{
    margin: 0;
    padding: 0;  
    overflow: hidden;
    background-color: #333;
    width: 100%;
}

ul li{
    display: block;
    float: left;
    color: rgb(255, 255, 255);
    text-align: center;
    font-size: 1.5rem;
    box-sizing: border-box;
    padding: 14px 16px; 
}

ul .pink{
    background-color: rgb(233, 62, 204);
    padding: 6px 16px 6px 16px;
    top: 7px;
    position: relative; 
    border-color: rgb(233, 62, 204);
    border-width: 1px;
    border-style: solid;
    border-radius: 5px;
}